<template>
  <div class="all">
      <div>
          <div class='stockInfo'>
            <div>股票名称: {{gupiao}}</div>
          </div>
          <div class='stockInfo'>
            <div>股票代码: {{gupiaocode}}</div>
          </div>
      </div>
      <div class='echar'>
          <div id="myChart" style="width:70%;height:200px;paddingLeft:0px"></div>
          <div>未来预测区</div>
      </div>

      <div class='stockInfo'>
          相似k线股票: {{xsgupiao}}
      </div>
      <div class='stockInfo'>
          相似股票代码: {{xsgupiaocode}}
      </div>
      <div class='stockInfo'>
          相似股票代码: {{xsdu}}
      </div>
      <div class='echar'>
          <div id="yourChart" style="width:70%;height:200px;paddingLeft:0px"></div>
          <div id="hisChart" style="width:30%;height:200px;paddingLeft:0px"></div>
      </div>
  </div>
</template>

<script>
import echarts from 'echarts'
import axios from 'axios'

export default {
  data(){
    return{
      gupiao: '查询中',
      xsgupiao: '查询中',
      gupiaocode: '',
      xsgupiaocode: '',
      datelist: [],
      valuebiglist: [],
      datelist2: [],
      valuebiglist2: [],
      datelist3: [],
      valuebiglist3: [],
      xsdu: '',
      max:'',
      min:'',
    }
  },
  mounted() {
    this.drawLine();
    this.fetchdata();
    this.drawLinexs();
    this.drawLinexsyuce();
  },
  watch:{
    max: function(){
      // debugger
      console.log('watch')
      this.drawLinexs()
      this.drawLinexsyuce()
    },
    min: function(){
      this.drawLinexs()
      this.drawLinexsyuce()
    },
  },
  methods: {
    fetchdata(){
      var that = this
      var valuelist = []
      that.xsgupiaocode = localStorage.getItem('xscode')
      axios.post('https://test-paladin.pingan.com.cn:10094/app/F011',{
        "symbol":localStorage.getItem('xscode'),
        "period":localStorage.getItem('xspreday'),
        "endDate":localStorage.getItem('xsendDate'),
        "lang":null,
        "orgId":"310003",
        "reqChl":"03",
        "reqTime":"20180727150130123",
        "serNum":"1234567890",
        "sign":"SHA256withRSA2048",
        "token":"D688D2555ED94C7285D26BDF4B13D08F",
        "tranCode":"F011",
        "version":"100"
      }).then(function(response){
        that.xsgupiao = response.data.indexDtl.sname
        response.data.indexDtl.indexList.map((value,key)=>{
          if(value.tOpen!=="0.0000"&&value.tClose!=="0.0000"&&value.low!=="0.0000"&&value.high!=="0.0000"){
            valuelist.push(value.low)
            valuelist.push(value.high)
          }
        })
        // debugger
        console.log(valuelist)
      }).then(function(response){
        that.xsgupiaocode = localStorage.getItem('xscode')
        axios.post('https://test-paladin.pingan.com.cn:10094/app/F012',{
          "symbol":localStorage.getItem('xscode'),
          "endDate":localStorage.getItem('xspredict'),
          "startDate":localStorage.getItem('xsendDate'),
          "lang":null,
          "orgId":"310003",
          "reqChl":"03",
          "reqTime":"20180727150130123",
          "serNum":"1234567890",
          "sign":"SHA256withRSA2048",
          "token":"D688D2555ED94C7285D26BDF4B13D08F",
          "tranCode":"F011",
          "version":"100"
        }).then(function(response){
          // debugger
          response.data.indexDtl.indexList.map((value,key)=>{
            if(value.tOpen!=="0.0000"&&value.tClose!=="0.0000"&&value.low!=="0.0000"&&value.high!=="0.0000"){
              valuelist.push(value.low)
              valuelist.push(value.high)
            }
          })
          console.log(valuelist)
          console.log(Math.max.apply(null,valuelist)+'1111111')
          console.log(Math.min.apply(null,valuelist)+'2222222')
          that.max = Math.max.apply(null,valuelist)
          that.min = Math.min.apply(null,valuelist)
        })
      }).then(function(response){
        // debugger
        console.log(valuelist)
        
      })
    },

    drawLine() {
      var that = this
      that.gupiao = localStorage.getItem('stock_name')
      that.gupiaocode = localStorage.getItem('stock_code').split('.')[0]
      axios.post('https://test-paladin.pingan.com.cn:10094/app/F011',{
        "symbol":that.gupiaocode,
        "period":"90",
        "endDate":"20181110",
        "lang":null,
        "orgId":"310003",
        "reqChl":"03",
        "reqTime":"20180727150130123",
        "serNum":"1234567890",
        "sign":"SHA256withRSA2048",
        "token":"D688D2555ED94C7285D26BDF4B13D08F",
        "tranCode":"F011",
        "version":"100"
      }).then(function(response){
        response.data.indexDtl.indexList.map((value,key)=>{
          value.tOpen!=="0.0000"&&value.tClose!=="0.0000"&&value.low!=="0.0000"&&value.high!=="0.0000"&&
          that.datelist.push(value.tDate)
        })
        response.data.indexDtl.indexList.map((value,key)=>{
          var valuelist = []
          if(value.tOpen!=="0.0000"&&value.tClose!=="0.0000"&&value.low!=="0.0000"&&value.high!=="0.0000"){
            valuelist.push(value.tOpen)
            valuelist.push(value.tClose)
            valuelist.push(value.low)
            valuelist.push(value.high)
            that.valuebiglist.push(valuelist)
          }
        })

      let chartK = echarts.init(document.getElementById("myChart"))

      var option = {
        backgroundColor: '#F6F8FB',
        grid: {
          top: '5%',
          left: '10%',
          right: '5%',
          bottom: '10%'
        },
        // tooltip: {
        //   trigger: 'axis',
        //   axisPointer: {
        //     animation: false,
        //     type: 'cross',
        //     lineStyle: {
        //       color: '#376df4',
        //       width: 1,
        //       opacity: 1
        //     }
        //   }
        // },
        xAxis: {
          data: that.datelist,
          type: 'category',
          boundaryGap: false,
          axisLine: {
            lineStyle: {
              color: '#fff'
            }
          },
          splitLine: {
            show: false
          },
          axisLabel: {
            show: true,
            textStyle: {
              color: '#6B7D97',
              fontSize: '10'
            }
          },
        },
        yAxis: {
          scale: true,
          type: 'value',
          axisLine: {
            lineStyle: {
              color: '#fff'
            }
          },
          axisLabel: {
            show: true,
            textStyle: {
              color: '#6B7D97',
              fontSize: '10'
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: '#fff',
              width: 1
            }
          },
          splitArea: {
            show: true,
            areaStyle: {
              color: '#F6F8FB'
            }
          }
        },

        dataZoom: [
          {
              type: 'inside',
              start: 0,
              end: 100,
              minValueSpan: 10
          },
          {
              show: false,
              type: 'slider',
              bottom: 0,
              start: 0,
              end: 100,
              minValueSpan: 10
          }
        ],
        animation: false,
        series: [
          {
            type: 'candlestick',
            name: '日K',
            data: that.valuebiglist,
            itemStyle: {
              normal: {
                color: '#FD1050',
                color0: '#0CF49B',
                borderColor: '#FD1050',
                borderColor0: '#0CF49B'
              }
            }
          },
        ]
      };
      chartK.setOption(option)
      })

    },

    drawLinexs(){
      var that = this
      that.xsdu = parseFloat(localStorage.getItem('xsdu')).toFixed(2)
      that.xsgupiaocode = localStorage.getItem('xscode')
      axios.post('https://test-paladin.pingan.com.cn:10094/app/F011',{
        "symbol":localStorage.getItem('xscode'),
        "period":localStorage.getItem('xspreday'),
        "endDate":localStorage.getItem('xsendDate'),
        "lang":null,
        "orgId":"310003",
        "reqChl":"03",
        "reqTime":"20180727150130123",
        "serNum":"1234567890",
        "sign":"SHA256withRSA2048",
        "token":"D688D2555ED94C7285D26BDF4B13D08F",
        "tranCode":"F011",
        "version":"100"
      }).then(function(response){
        console.log(response)
      that.xsgupiao = response.data.indexDtl.sname
      that.datelist2 = []
      response.data.indexDtl.indexList.map((value,key)=>{
        that.datelist2.push(value.tDate)
      })
      that.valuebiglist2 = []

      response.data.indexDtl.indexList.map((value,key)=>{
        var valuelist = []
        valuelist.push(value.tOpen)
        valuelist.push(value.tClose)
        valuelist.push(value.low)
        valuelist.push(value.high)
        // debugger
        that.valuebiglist2.push(valuelist)
      })

      let chartK2 = echarts.init(document.getElementById("yourChart"))
      
      // debugger
      var option = {
        backgroundColor: '#F6F8FB',
        grid: {
          top: '5%',
          left: '10%',
          right: '0%',
          bottom: '10%'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            animation: false,
            type: 'cross',
            lineStyle: {
              color: '#376df4',
              width: 1,
              opacity: 1
            }
          }
        },
        xAxis: {
          data: that.datelist2,
          type: 'category',
          boundaryGap: false,
          axisLine: {
            lineStyle: {
              color: '#fff'
            }
          },
          splitLine: {
            show: false
          },
          axisLabel: {
            show: true,
            textStyle: {
              color: '#6B7D97',
              fontSize: '10'
            }
          },
        },
        yAxis: {
          scale: true,
          type: 'value',
          max:(that.max&&that.max!==0)&&that.max,
          min:(that.min&&that.min!==0)&&that.min,
          axisLine: {
            lineStyle: {
              color: '#fff'
            }
          },
          axisLabel: {
            show: true,
            textStyle: {
              color: '#6B7D97',
              fontSize: '10'
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: '#fff',
              width: 1
            }
          },
          splitArea: {
            show: true,
            areaStyle: {
              color: '#F6F8FB'
            }
          }
        },

        dataZoom: [
          {
              type: 'inside',
              start: 0,
              end: 100,
              minValueSpan: 10
          },
          {
              show: false,
              type: 'slider',
              bottom: 0,
              start: 0,
              end: 100,
              minValueSpan: 10
          }
        ],
        animation: false,
        series: [
          {
            type: 'candlestick',
            name: '日K',
            data: that.valuebiglist2,
            itemStyle: {
              normal: {
                color: '#FD1050',
                color0: '#0CF49B',
                borderColor: '#FD1050',
                borderColor0: '#0CF49B'
              }
            }
          },
        ]
      };
      chartK2.setOption(option)
      })
    },

    drawLinexsyuce(){
      var that = this
      that.xsgupiaocode = localStorage.getItem('xscode')
      axios.post('https://test-paladin.pingan.com.cn:10094/app/F012',{
        "symbol":localStorage.getItem('xscode'),
        "endDate":localStorage.getItem('xspredict'),
        "startDate":localStorage.getItem('xsendDate'),
        "lang":null,
        "orgId":"310003",
        "reqChl":"03",
        "reqTime":"20180727150130123",
        "serNum":"1234567890",
        "sign":"SHA256withRSA2048",
        "token":"D688D2555ED94C7285D26BDF4B13D08F",
        "tranCode":"F011",
        "version":"100"
      }).then(
        function(response){
          that.xsgupiao = response.data.indexDtl.sname
          that.datelist3=[]
          response.data.indexDtl.indexList.map((value,key)=>{
          that.datelist3.push(value.tDate)
        })
        that.valuebiglist3 = []
        response.data.indexDtl.indexList.map((value,key)=>{
          var valuelist = []
          valuelist.push(value.tOpen)
          valuelist.push(value.tClose)
          valuelist.push(value.low)
          valuelist.push(value.high)
          that.valuebiglist3.push(valuelist)
        })

      let chartK3 = echarts.init(document.getElementById("hisChart"))

      var option = {
        backgroundColor: '#85a5ff',
        grid: {
          top: '5%',
          left: '10%',
          right: '5%',
          bottom: '10%'
        },
        // tooltip: {
        //   trigger: 'axis',
        //   axisPointer: {
        //     animation: false,
        //     type: 'cross',
        //     lineStyle: {
        //       color: '#376df4',
        //       width: 1,
        //       opacity: 1
        //     }
        //   }
        // },
        xAxis: {
          show:false,
          data: that.datelist3,
          type: 'category',
          boundaryGap: false,
          axisLine: {
            lineStyle: {
              color: '#fff'
            }
          },
          splitLine: {
            show: false
          },
          axisLabel: {
            show: false,
            textStyle: {
              color: '#6B7D97',
              fontSize: '10'
            }
          },
        },
        yAxis: {
          show:false,
          scale: true,
          type: 'value',
          max:(that.max&&that.max!==0)&&that.max,
          min:(that.min&&that.min!==0)&&that.min,
          axisLine: {
            lineStyle: {
              color: '#fff'
            }
          },
          axisLabel: {
            show: false,
            textStyle: {
              color: '#6B7D97',
              fontSize: '10'
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: '#fff',
              width: 1
            }
          },
          splitArea: {
            show: true,
            areaStyle: {
              color: '#85a5ff'
            }
          }
        },

        dataZoom: [
          {
              type: 'inside',
              start: 0,
              end: 100,
              minValueSpan: 10
          },
          {
              show: false,
              type: 'slider',
              bottom: 0,
              start: 0,
              end: 100,
              minValueSpan: 10
          }
        ],
        animation: false,
        series: [
          {
            type: 'candlestick',
            name: '日K',
            data: that.valuebiglist3,
            itemStyle: {
              normal: {
                color: '#FD1050',
                color0: '#0CF49B',
                borderColor: '#FD1050',
                borderColor0: '#0CF49B'
              }
            }
          },
        ]
      };
      chartK3.setOption(option)
      })
    }
  }
}
</script>

<style scoped>
.echar{
    display: flex;
    padding-bottom: 20px;
}
.all{
    padding:10px 16px;
  }
.top{
    display: flex;
    font-size: 16px;
}
.stockInfo{
    display: flex;
    flex:1;
    height:30px;
    font-size: 16px;
}

.mychart{
    width:300px;
    height:300px;
}
  .indus-title-a{
    display: block;
    float: right;
    font-size:14px;
    color:#11089c;
  }
</style>




