<template>
  <div class="photo">
    <h1>发现身边的投资机会</h1>
    <div class="photo-input">
      <input class="photo-file" type="file" @change="fileImg">
      <div class="photo-button">
        <img class="photo-logo" src="../../assets/photo-w.png" alt="">
        <span>拍照识财</span>
      </div>
    </div>
    <!--<img :src="imgBase64" alt="">-->
  </div>
</template>

<script>
export default {
  name: 'photo',
  data () {
    return {
      imgBase64: ''
    }
  },
  mounted () {
  },
  methods: {
    fileImg (e) {
      let reader = new FileReader()
      // reader.readAsDataURL(e.target.files[0])
      reader.onload = (ev) => {
        console.log(reader.result)
        this.imgBase64 = reader.result
        localStorage.setItem('imgBase64', reader.result)
        // alert('dede')
        this.$router.push('/result/industry')
      }
      reader.readAsDataURL(e.target.files[0])
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.photo{
  background: #fff;
  padding:20px;
}
.photo h1{
  display: block;
  font-size:20px;
  padding:10px 0;
}
.photo-input{
  display: block;
  position: relative;
  height:80px;
  margin:50px 0;
}
.photo-file{
  display:block;
  width:100%;
  height:50px;
  position: absolute;
  z-index:2;
  opacity: 0;
}
.photo-button{
  display: flex;
  position: absolute;
  top: 0;
  background: #4CAF50;
  width: 80%;
  border-radius: 50px;
  height: 50px;
  left: 10%;
  align-items: center;
  justify-content: center;
}
.photo-button img{
  display: block;
  width:34px;
}
.photo-button span{
  display: block;
  font-size:16px;
  color:#fff;
  padding:0 8px;
}
</style>



