<template>
    <div>
        <div class='top clearfix'>
            <img class="photo-img" :src="imgBase64" alt="">
            <div class='topright'>
              <div class="key-words">识别物体: {{keyw}}</div>
              <div class="key-words">所属行业: {{indus}}</div>
            </div>
        </div>
        <a class="search" href="#/">重新识别</a>
        <div class='main' v-if="loading==false">
            <div class='linkGroup'>
                <router-link to='/result/stock' class='link'>股票</router-link>
                <router-link to='/result/industry' class='link'>行业</router-link>
                <router-link to='/result/fund' class='link'>基金</router-link>
            </div>
            <router-view></router-view>
        </div>
        <div v-if="loading==true">
          <div class="loading" v-if="loadingText.length === 0">数据加载中...</div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import { debug } from 'util'
export default {

  data () {
    return {
      imgBase64: localStorage.getItem('imgBase64'),
      keyw: '处理中...',
      indus: '处理中...',
      loading: true,
      loadingText:''
    }
  },
  mounted() {
    var that = this

    axios.post('https://test-paladin.pingan.com.cn:10094/rbpea/api/ea1001/',{
      img: localStorage.getItem('imgBase64')
    }).then(function (response){
      if (response.data.response_code == 200) {
        that.keyw = response.data.goods_desc[0].goods,
        that.indus = response.data.goods_desc[0].industry_name
        localStorage.setItem("stock_code", response.data.goods_desc[0].stock_code)
        localStorage.setItem("stock_name", response.data.goods_desc[0].stock_name)
        localStorage.setItem("fund_code", response.data.goods_desc[0].fund_code)
        localStorage.setItem("fund_name", response.data.goods_desc[0].fund_name)
        localStorage.setItem("industry_code", response.data.goods_desc[0].industry_code)
        localStorage.setItem("industry_name", response.data.goods_desc[0].industry_name)
      } else{
        alert('402')
        that.loaingText = '图片识别失败'
      }

    }).then(function(response){
      let stock_code = localStorage.getItem("stock_code")
      // var that = this
      axios.post('https://test-paladin.pingan.com.cn:10094/rbpea/api/ea1002/',{
        right:'20181217',
        stock_ID: stock_code,
        pre_monthes:12,
        predict_days:90,
        number_of_stock:3
      }).then(function(response){
        that.loading = false
        localStorage.setItem("xscode",response.data.out_desc.code[0])
        localStorage.setItem("xsendDate", response.data.out_desc.end[0])
        localStorage.setItem("xspredict", response.data.out_desc.predict[0])
        localStorage.setItem("xspreday", response.data.out_desc.pre_sub_days[0])
        localStorage.setItem("xsdu", response.data.out_desc.score[0])
      }).catch(function(error){
        console.log(error)
      })
    }).catch(function(error){
      // that.loading = false
      that.loaingText = '图片识别失败'
      console.log(error)
      alert("图片识别失败")
      this.$router.push({name:'search'});


    })

  },
  methods:{
    // reidentification(){
    //   this.$router.push({name:'search'});
    // }
  },
}
</script>

<style scoped>
.main{
  background: #fff;
}
.top{
  background-color: #F6F8FB;
  padding:20px 20px 20px;
  display:flex;
}
.search{
  /* float:right; */
}
.topright{
  flex:1;
  padding: 50px 0 0 0;
}
.photo-img {
  display: inline-block;
  height: 160px;
  max-width: 58%;
  vertical-align: top;
  border-radius:10px;
}
.key-words {
  /* display: inline-block; */
  font-size: 16px;
  /* padding: 40px 0 0 20px; */
  text-align: center;
  padding:4px 0 0;
}
.key-words em{
  display: inline-block;
  font-size:16px;
  vertical-align: middle;
}
.linkGroup{
  display: flex;
  flex-direction: row;
  height:40px;
  border-bottom: 1px solid #e8e8e8;
  margin-bottom: 10px;
}
.router-link-active{
  color: #000!important;
  border-bottom: 2px solid #727272;
}
.link{
  color: #8c8c8c;
  flex:1;
  margin: 0px 20px 0px 20px;
  text-align: center;
  line-height: 40px;
  font-size: 16px;
}
.search {
  display: block;
  font-size: 16px;
  text-align: left;
  padding: 0px 39px 16px;
  color: #1b1b9c;
  background: #f5f8fb;
  margin-top: -14px;
}
.loading{
  display: block;
  font-size:16px;
  text-align: center;
  padding:40px 0;
}
</style>



